import React from 'react';
import './App.css';
import EachCountry from './components/each-country/each-country.component'
import CountryList from './components/country-list/country-list.component'
import Header from './components/header/header.component'
import Api from './components/apis/apis.component'
import {Switch , Route } from 'react-router-dom'


class App extends React.Component {
  constructor(props){
    super(props)
    this.state = {
      countryList : [],
      loading:true,
    }
  }
  componentDidMount(){
    fetch("https://api.covid19api.com/countries")
    .then(res=>res.json())
    .then(data=>{
      this.setState((s)=>{
        return({countryList:data,loading:false})
      })
    })
  }
  render(){
    return (
      <div className="coutainer-list-container">
      <Header></Header>
      <Switch>
        <Route exact path="/" component={(props)=>{
          return(<CountryList countryList={this.state.countryList}></CountryList>)
        }}>
        </Route>
        <Route exact path="/api" component={Api}></Route>
        <Route exact path="/:country/" render={(props)=>{return (<EachCountry></EachCountry>)}}/>
      </Switch> 
      {this.state.loading?<h1
        style={{
          width:"100%",
          textAlign:"center"
        }}
      >Loading..</h1>:null}
      </div>
      
    )
  }
}

export default App;
